form.user {
    .custom-checkbox.small {
        label {
            line-height: 1.5rem;
        }
    }

    .form-control-user {
        font-size: 0.8rem;
        border-radius: 4px;
        padding: 1.5rem 1rem;
    }

    .btn-user {
        font-size: 0.8rem;
        border-radius: 4px;
        padding: 0.75rem 1rem;

    }
}

